import Link from "next/link";


export default function Formm() {
    return (
        <>
            <div className="flex justify-center">
                <form>

                    <div className="flex justify-center space-x-[400px] mr-[260px]">
                        <div className="mb-5 w-10">
                            <label htmlFor="nom" className="mb-1.5 block text-center text-sm" >Nom</label>
                            <input className=" py-4 border-gray-100  shadow-md focus:border-blue w-[340px] h-7 rounded-lg border p-2 text-left text-slate-500 focus:outline-[#EBA565] bg-grey" type="text" placeholder="" id="nom" required />
                        </div>
                        <div className="w-10">
                            <label htmlFor="prénom" className="mb-1.5 block text-center text-sm">prénom</label>
                            <input className=" py-4 border-gray-100 shadow-md focus:border-blue w-[340px] h-7 rounded-lg border p-2 text-left text-slate-500 focus:outline-[#EBA565]" type="text" placeholder="" id="prénom" required />
                        </div>
                    </div>


                    <div className="arabic-only flex justify-center space-x-[400px] mr-[260px] ">
                        <div className="mb-5 w-10">
                            <label htmlFor="nom_ar" className="mb-1.5 block text-center text-sm">Nom(arab)</label>
                            <input className="  py-4 arabic-only border-gray-100 shadow-md focus:border-blue w-[340px] h-7 rounded-lg border p-2 text-left text-slate-500 focus:outline-[#EBA565]" type="text" pattern="[\u0600-\u06FF]+" placeholder="" id="nom_ar" required />
                        </div>
                        <div className="w-10">
                            <label htmlFor="prenom_ar" className="mb-1.5 block text-center text-sm">Prénom(arab)</label>
                            <input className=" py-4 border-gray-100 shadow-md focus:border-blue w-[340px] h-7 rounded-lg border p-2 text-left text-slate-500 focus:outline-[#EBA565]" type="text" placeholder="" id="prenom_ar" required />
                        </div>
                    </div>


                    <div className="flex justify-center space-x-[400px] mr-[260px] ">
                        <div className="mb-5 w-10">
                            <label htmlFor="father_name" className="mb-1.5 block w-40 text-sm">Prénom du père</label>
                            <input className=" py-4 border-gray-100 shadow-md focus:border-blue w-[340px] h-7 rounded-lg border p-2 text-left text-slate-500 focus:outline-[#EBA565]" type="text" placeholder="" id="father_name" required />
                        </div>
                        <div className="w-10">
                            <label htmlFor="mother_name" className="mb-1.5 block w-40  text-sm">Prénom de la mère</label>
                            <input className="py-4 border-gray-100 shadow-md focus:border-blue w-[340px] h-7 rounded-lg border p-2 text-left text-slate-500 focus:outline-[#EBA565]" type="text" placeholder="" id="mother_name" required />
                        </div>
                    </div>


                    <div className="flex justify-center space-x-[400px] mr-[260px]">
                        <div className="mb-5 w-10">
                            <label htmlFor="sexe" className="block text-left w-40 mb-2 text-sm font-medium text-gray-900 dark:text-white">Sexe</label>
                            <select id="sexe" className="h-9 w-[340px] shadow-md rounded-lg border  text-left text-slate-500 focus:outline-[#EBA565]">
                                <option selected>Sélectionnez votre sexe</option>
                                <option value="FM">femme</option>
                                <option value="HM">homme</option>
                            </select>
                        </div>
                        <div className="w-10 my-1">
                            <label htmlFor="DN" className="mb-1.5 block w-40 text-left text-sm">Date de nainaissance </label>
                            <input id="DN" className="py-4 border-gray-100 shadow-md w-[340px] focus:border-blue h-7 rounded-lg border p-2 text-left text-slate-500 focus:outline-[#EBA565]" type="date" placeholder="Date de nainaissance" required />
                        </div>
                    </div>



                    <div className="flex justify-center space-x-[400px] mr-[260px]">
                        <div className="mb-5 w-10">
                            <label htmlFor="nationalite" className="mb-1.5 block text-center text-sm">  Nationalité </label>
                            <input className="py-4 border-gray-100 shadow-md focus:border-blue w-[340px] h-7 rounded-lg border p-2 text-left text-slate-500 focus:outline-[#EBA565]" type="text" placeholder="" id="nationalite" required />
                        </div>
                        <div className="w-10">
                            <label htmlFor="paye" className="mb-1.5 block text-left text-sm w-40">  Pays de résidence  </label>
                            <input className="py-4 border-gray-100 shadow-md focus:border-blue w-[340px] h-7 rounded-lg border p-2 text-left text-slate-500 focus:outline-[#EBA565]" type="text" placeholder="" id="paye" required />
                        </div>
                    </div>


                    <div className="flex justify-center space-x-[400px] mr-[260px] mb-4">
                        <div className="mb-5 w-10">
                            <label htmlFor="nin" className="mb-1.5 block text-center text-sm">NIN</label>
                            <input className="py-4 border-gray-100  shadow-md focus:border-blue w-[340px] h-7 rounded-lg border p-2 text-left text-slate-500 focus:outline-[#EBA565]" type="number" placeholder="" id="nin" required />
                        </div>
                        <div className="w-10">
                            <label htmlFor="DE" className="mb-1.5 block text-left text-sm w-40">  Date d’expiration </label>
                            <input className="py-4 border-gray-100 shadow-md focus:border-blue w-[340px] h-7 rounded-lg border p-2 text-left text-slate-500 focus:outline-[#EBA565]" type="date" placeholder="Date d’expiration" id="DE" required />
                        </div>
                    </div>



                    <div className="flex justify-center ">

                        <button type="submit" className="border-orange-400 shadow-md h-15 mb-10 block w-[340px] rounded-lg border px-4 py-2 text-center font-bold text-black" >
                            <Link href={"/inscription/mahrampage"} className="font-bold">
                                Confirmer
                            </Link>
                        </button>
                    </div>

                </form></div>

        </>

    );
}
